:root {
  --header-height: 3rem;
  --nav-width: 68px;
  --first-color: #4723D9;
  --first-color-light: #AFA5D9;
  --dark-color: #272727;
  --body-font: 'Nunito', sans-serif;
  --normal-font-size: 1rem;
  --z-fixed: 100
}

*,
::before,
::after {
  box-sizing: border-box
}

a {
  text-decoration: none
}

.header {
  width: 100%;
  height: 3rem;
  height: var(--header-height);
  position: fixed;
  top: 0;
  left: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
  align-items: center;
  -webkit-justify-content: space-between;
  justify-content: space-between;
  padding: 0 1rem;
  background-color: #272727;
  background-color: var(--dark-color);
  z-index: 100;
  z-index: var(--z-fixed);
  transition: .5s
}

.header_toggle {
  color: #4723D9;
  color: var(--first-color);
  font-size: 1.5rem;
  cursor: pointer
}

.header_img {
  width: 35px;
  height: 35px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
  justify-content: center;
  border-radius: 50%;
  overflow: hidden
}

.header_img img {
  width: 40px
}

.l-navbar {
  position: fixed;
  top: 0;
  left: -30%;
  width: 68px;
  width: var(--nav-width);
  height: 100vh;
  background-color: #4723D9;
  background-color: var(--first-color);
  padding: .5rem 1rem 0 0;
  transition: .5s;
  z-index: 100;
  z-index: var(--z-fixed)
}

.nav {
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
  flex-direction: column;
  -webkit-justify-content: space-between;
  justify-content: space-between;
  overflow: hidden
}

.nav_logo,
.nav_link {
  display: grid;
  grid-template-columns: -webkit-max-content -webkit-max-content;
  grid-template-columns: max-content max-content;
  -webkit-align-items: center;
  align-items: center;
  grid-column-gap: 1rem;
  -webkit-column-gap: 1rem;
  column-gap: 1rem;
  padding: .5rem 0 .5rem 1.5rem
}

.nav_logo {
  margin-bottom: 2rem
}

.nav_logo-icon {
  font-size: 1.25rem;
  color: #272727;
  color: var(--dark-color)
}

.nav_logo-name {
  color:whitesmoke;
  font-weight: 700
}

.nav_link {
  position: relative;
  color: #AFA5D9;
  color: var(--first-color-light);
  margin-bottom: 1.5rem;
  transition: .3s
}

.nav_link:hover {
  color: greenyellow;
}

.nav_icon {
  font-size: 1.25rem
}

.show {
  left: 0
}

.body-pd {
  padding-left: calc(68px + 1rem);
  padding-left: calc(var(--nav-width) + 1rem)
}

.active {
  color: rgb(232, 235, 51);
}

.active::before {
  content: '';
  position: absolute;
  left: 0;
  width: 2px;
  height: 32px;
  background-color: #272727;
  background-color: var(--dark-color)
}

#example_length{
  margin-right: 15px;
  margin-top: 8px;
}

.height-100 {
  height: 100vh
}

